import React from "react";
import { ZappHOC } from "zerod";
// 路由组件
import lazyLoad from "@/lazyLoad/lazyLoad";
const Main = lazyLoad(() => import("@/views/Main/"));

const publiced = require("../config/pablic");
const pageConfig = {
	rootRoutes: [
		{
			path: "/main",
			component: Main,
		},
		{
			redirect: true, //重定向
			path: "/",
			to: "/main/start-doc",
		},
		{
			redirect: true,
			path: "/main", //重定向
			to: "/main/start-doc",
		},
	],
	footerLinks: null,
	// footerCopyright: "2018 华云中盛-政务事业部技术团队出品",
	routerType: publiced.path == "/" ? "history" : "hash",
};
export default ZappHOC(pageConfig);
if (publiced.path !== "/")
	window.addEventListener(
		"hashchange",
		() => {
			window.Pace && window.Pace.restart();
		},
		false,
	);
